// Importing Reset File
@import '_reset.scss';

// Importing Main Styles File
@import '_main.scss';

// Start Coding From Here

*{
   font-family: 'Roboto', sans-serif;
 }
 
 header {
   nav {
      box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
      margin-bottom: 20px;
      .navbar-brand {
         font-weight: bold;
         font-size: 24px;
         color: #676767!important;
         font-family: 'Permanent Marker', cursive;
         img{
            width: 45px;
            height: 45px;
         }
       }
       .nav-link {
         font-family: 'Zilla Slab', serif;
         font-weight: bold;
       }
    }
 }
 
 .app-header {
    h1{
      font-size: 50px;
      font-family: 'Zilla Slab', serif;
      font-weight: 900;
      &:after{
         content: '';
         width: 350px;
         height: 2px;
         border-radius: 50%;
         background: #676767;
         display: block;
         margin: 10px 0;
       }
    }
 }

 .weather-app{
   label{
      display: block;
      font-weight: bold;
      font-size: 25px;
      margin-top: 20px;
      font-family: 'Zilla Slab', serif;
   }
   input{
      display: inline-block;
      margin: 10px 0 30px;
      border: 2px solid rgba(0, 0, 0, 0.2);
      width: 74%;
      border-radius: 15px 0 0 15px;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
      padding: 15px;
      font-family: 'Zilla Slab', serif;
   }
   button{
      display: inline-block;
      margin: 10px 0 30px;
      border: none;
      width: 25%;
      border-radius: 0 15px 15px 0;
      border: 2px solid rgba(0, 0, 0, 0.2);
      padding: 15px;
      font-weight: bold;
      font-family: 'Zilla Slab', serif;
      &:hover{
         transform: none;
         background: royalblue;
      }
   }
   .loading{
      border: 1px solid #676767;
      font-weight: bold;
      text-align: center;
      padding: 15px;
      font-size: 25px;
   }
   .err {
      border: 1px solid #910909;
      color: #910909;
      font-weight: bold;
      padding: 15px;
      font-size: 25px;
      box-shadow: 0 2px 8px rgba(111, 3, 3, 0.4);
   }
   .weather-state{
      h3{
         background: #f6f6f6;
         padding: 20px;
         font-family: 'Zilla Slab', serif;
         color: #002147;
         box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
      }
   }
 }

 .notes-app{
   label{
      display: block;
      font-weight: bold;
      font-size: 25px;
      margin-top: 20px;
      font-family: 'Zilla Slab', serif;
      text-align: center;
   }
   .noteTitle, .noteBody{
      display: block;
      width: 65%;
      margin: 10px auto;
      border: 2px solid rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      font-family: 'Zilla Slab', serif;
   }
   button{
      padding: 10px 50px;
      font-family: 'Zilla Slab', serif;
      border-radius: 5px;
      font-weight: bold;
      background: #910909;
      margin: 10px auto;
      display: block;
   }
   .allNotes{
      padding: 20px 0;
      .note{
         position: relative;
         background: #f6f6f6;
         margin: 15px 0;
         padding: 13px 20px;
         border-radius: 5px;
         box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
         h4{
            font-weight: bold;
         }
         button{
            position: absolute;
            bottom: 15px;
            right: 15px;
            padding: 3px 8px;
            border: none;
            margin: 0;
         }
      }
   }
 }

 footer {
   margin-top: 45px;
   border-top: 1px solid #676767;
   padding: 20px;
   color: #676767;
   font-size: 18px;
 }